.header-wrapper {
  background-color: transparent;
  position: fixed;
  padding-top: 10px;
  top: 0;
  left: 0;
  z-index: 112312312;
}
.header__logo-wrapper {
  width: 150px;
  display: block;
  float: left;
  font-family: Lato-Regular;
}
