  position: absolute;
  border-radius: 2px;
  width: 80%;
  display: block;
  height: 4px;
  left: 10%;
  background-color: #1eaf26;
  transition: 0.2s linear all;
}
.header__toggle-item:nth-child(1) {
  top: 5px;
}
.header__toggle-item:nth-child(2) {
  top: 50%;
  transform: translateY(-50%);
}
.header__toggle-item:nth-child(3) {
  bottom: 5px;
}
